.header {
	padding: var(--margin-800) var(--margin-400);
	text-align: center;

	background-image: url(../images/bg-header-mobile.png);
	background-repeat: no-repeat;
	background-size: 100%;
}

.header__heading {
	margin-top: var(--margin-700);
	color: var(--clr-neutral-900);
	font-size: var(--fs-900);
	font-weight: 600;
	line-height: 1.2;
}

.header__logo {
	display: inline;
	width: 6.25rem;
}

.header__paragraph {
	margin-top: var(--margin-300);
	margin-bottom: var(--margin-600);
}

.link-btn:last-child {
	margin-top: var(--margin-300);
}

/* Large devices */
@media screen and (min-width: 33.687em) {
	.header {
		background-image: url(../images/bg-header-desktop.png);
	}

	.header__logo {
		width: 7.8125rem;
	}

	.link-btn:last-child {
		margin-top: 0;
		margin-left: var(--margin-300);
	}
}

/* Very large devices */
@media screen and (min-width: 89.937em) {
	.header__paragraph {
		padding-left: var(--margin-900);
		padding-right: var(--margin-900);
	}
}
